import React, { Component } from "react"
import { Link } from "react-router-dom"
import Login from "../components/Login"
import SpinnerLoading from "../components/SpinnerLoading"
import * as firebase from "firebase"
import FichaExpreso from "../components/FichaExpreso"
import "./Styles/ProovedoresList.css"
import { changeExpreso } from "../actions/index"
import { connect } from "react-redux"
import { confirmAlert } from 'react-confirm-alert'; // Import
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faPlus} from '@fortawesome/free-solid-svg-icons'
class ExpresosList extends Component {
    returnBack() {
        this.props.changeExpreso(null)
    }
    handleChange(e) {
        var forEach = Array.prototype.forEach;
        var arrayClientes = document.getElementsByClassName("btn-cliente")
        console.log(arrayClientes)
        forEach.call(arrayClientes, function (f) {
            if (f.innerHTML.toLowerCase().search(e.target.value.toLowerCase()) == -1)
                f.parentNode.parentNode.style.display = "none";
            else
                f.parentNode.parentNode.style.display = "block";
        });
    }
    alertNuevoExpreso = () =>{
        confirmAlert({
            customUI: ({ onClose }) => {
            return (
                <div className='custom-ui'>
                    <div className="container alert">
                        <div className="row">
                            <div className="col-12 text-center">
                                <FontAwesomeIcon className="alertIcon plusIcon" icon={faPlus}  />
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-12 text-center">
                                <h1>Agregar Nuevo Expreso</h1>
                            </div>
                        </div>
                        <form>
                            <div className="form-row">
                                <div className="col form-group">
                                    <input autoFocus type="text" class="form-control" id='nombreNuevoExpreso' placeholder="Nombre del Expreso"/>
                                </div>
                            </div>
                            <div className="form-row">
                                <div className="col form-group text-center">
                                    <button
                                        type='submit'
                                        className='btn btn-success'
                                        onClick={() => {
                                            this.nuevoExpreso()
                                            onClose();
                                        }}
                                    >
                                        Agregar
                                    </button>
                                </div>
                                <div className="col text-center">
                                    <button
                                        className='btn btn-outline-dark'
                                        onClick={() => {
                                            onClose();
                                        }}
                                    >
                                        Cancelar
                                    </button>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
            );
        }
    });
}
    nuevoExpreso = async () =>{
        const nombre = document.getElementById("nombreNuevoExpreso").value
        if(nombre){
            firebase.database().ref().child(this.props.user.uid).child('expresos').update({
                [nombre]:{
                    datos:{
                        nombre:nombre,
                    }
                }
            })
        }
    }
    render() {
        try {
            const user = this.props.user.email
            if (user) {
                if (this.props.expreso) {
                    console.log(this.props.expreso)
                    const expreso = this.props.expresos[this.props.expreso]
                    if (expreso) {
                        return (
                            <div>
                                <FichaExpreso proovedor={expreso} seccion="expresos" handleClick={e => {
                                    this.returnBack()
                                }} />
                            </div>
                        )
                    }
                    else {
                        return (
                            <div>
                                <div className="productList">
                                    <div className="container">
                                        <div className="row">
                                            <div className="col text-center">
                                                <h1 className="title">No existe el Expreso</h1>
                                                
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        )
                    }
                }
                else {
                    return (
                        <div>
                            <div className="productList">
                                <div className="container-fluid">
                                    <div className="row">
                                        <div className="col-6 offset-3">
                                            <form>
                                                <div className="formRow">
                                                    <div className="form-group col-12 text-center">
                                                        <input autoFocus type="text" className="form-control" onChange={
                                                            e => {
                                                                this.handleChange(e)
                                                            }
                                                        } placeholder="Buscar Cliente" />
                                                    </div>

                                                </div>
                                            </form>
                                        </div>
                                        <div className="col">
                                            <button type="button" className="btn btn-success" onClick={e=>{
                                                this.alertNuevoExpreso()
                                            }}>
                                                Agregar Nuevo Expreso
                                            </button>
                                        </div>
                                    </div>
                                    <div className="row">
                                        {this.props.expresos?
                                            Object.values(this.props.expresos).map(expreso => (
                                                <div className="col-12 col-md-6 col-lg-4 text-center form-group">
                                                    <Link to={{
                                                        pathname: `/Expresos/`,
                                                        search: `${expreso.datos.nombre}`
                                                    }}>
                                                        <button type="button" className="btn btn-outline-light btn-cliente" onClick={() => {
                                                            this.props.changeExpreso(expreso.datos.nombre)
                                                        }}>
                                                            {expreso.datos.nombre}
                                                        </button>
                                                    </Link>
                                                </div>
                                            ))
                                            :
                                            <>
                                                <div className="col-12 text-center">
                                                    <h1 className='text-white'>Aun No Hay Expersos!</h1>
                                                </div>
                                            </>
                                        }
                                    </div>
                                </div>
                            </div>
                        </div>
                    )

                }
            }
        }
        catch{
            return (
                <Login redirect="Clientes" />
            )
        }
    }
}

const mapStateToProps = state => ({
    user: state.user,
    expreso: state.expreso,
    expresos: state.expresos
})
const mapDispatchToProps = {
    changeExpreso,

}
export default connect(mapStateToProps, mapDispatchToProps)(ExpresosList)