import React , {Component} from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {  faArrowRight} from '@fortawesome/free-solid-svg-icons'
import "./styles/Product.css"
import { confirmAlert } from 'react-confirm-alert'; // Import
import * as firebase from "firebase"
import { connect } from "react-redux"

import ProductoCadena from './ProductoCadena'
class CadenaDeProduccion extends Component{

    alertConfiguracionCadena=()=>{
        confirmAlert({
            customUI: ({ onClose }) => {
            return (
                <div className='custom-ui'>
                    <div className="container alert">
                        <div className="row">
                            <div className="col-12 text-center">
                                <h1>Configuracion De Cadena De Produccion</h1>
                                <h3>{this.props.nombre}</h3>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col">
                                <p>En esta seccion podra editar la cadena de Produccion</p>
                            </div>
                        </div>
                        <form>
                            <div className="form-row">
                                <div className="col form-group text-center">
                                    <button
                                        type='submit'
                                        className='btn btn-outline-danger'
                                        onClick={() => {
                                            this.eliminarCadena()
                                        }}
                                    >
                                        Eliminar cadena de produccion
                                    </button>
                                </div>
                            </div>
                            <div className="form-row">
                                <div className="col form-group text-center">
                                    <button
                                        type='submit'
                                        className='btn btn-success'
                                        onClick={() => {
                                            this.configuracionProducto()
                                            onClose();
                                        }}
                                    >
                                        Aceptar
                                    </button>
                                </div>
                                <div className="col text-center">
                                    <button
                                        className='btn btn-outline-dark'
                                        onClick={() => {
                                            onClose();
                                        }}
                                    >
                                        Cancelar
                                    </button>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
              );
            }
        });
    }
    eliminarCadena = async () =>{
        confirmAlert({
            title: 'Eliminar Cadena de Producccion De '+this.props.nombre+'?',
            buttons: [
                {
                    label: 'Si, Eliminar',
                    onClick: async () => {
                        await firebase.database().ref().child(this.props.user.uid).child('cadenasDeProduccion').child(this.props.nombre).remove()
                        
                    }
                },
                {
                    label: 'Cancelar'
                }
            ]
        });
    }
    render(){
        return(
            <div className="col-12 col-md-6 col-lg-4 d-flex align-items-center justify-content-center form-group">
                <div class="card text-center cadena">
                    <div class="card-body" onClick={this.alertConfiguracionCadena}>
                        <h5 class="card-title">{this.props.nombre}</h5>
                        {this.props.cadena.map((articulo,index)=>(
                            <ProductoCadena
                                articulo={articulo}
                                cadena={this.props.cadena}
                                index={index}
                            />
                        ))}
                    </div>
                </div>
            </div>
        )
    }
}
const mapStateToProps = state => {
    return {
        user:state.user,
    }
}
export default connect(mapStateToProps, null)(CadenaDeProduccion)